import axios from "axios";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Select from "react-select";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const EditDoctor = () => {
  const { id } = useParams(); // this gets the ID from the URL

  const [doctor, setDoctor] = useState({
    name: "",
    specialization: "",
    qualification: "",
    profilePhoto: null,
    experience: "",
    aboutDoctor: "",
    profilePhotoUrl: "",
  });

  const navigate = useNavigate();

  // Handle text input
  const handleChange = (e) => {
    const { name, value } = e.target;
    setDoctor((prev) => ({ ...prev, [name]: value }));
  };

  // Handle file input
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
           const allowedFormats = /(\.jpg|\.jpeg|\.png)$/i;
            if (!allowedFormats.exec(file.name)) {
              toast.error(
                "Profile photo of doctor should be in .jpeg | .jpg | .png format"
              );
              e.target.value = "";
              return;
            }

               const maxSize = 2 * 1024 * 1024; // 5 MB in bytes
                if (file.size > maxSize) {
                  toast.error("Profile photo size should not exceed 2 MB");
                  e.target.value = "";
                  return;
                }

      setDoctor((prev) => ({
        ...prev,
        profilePhoto: file,
          profilePreview: URL.createObjectURL(file), 
      }));
    }
  };

  // Fetch doctor details by ID
  useEffect(() => {
    const getDoctorDetailsByID = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/doctors/${id}`
        );

        setDoctor({
          name: res.data.name || "",
          specialization: res.data.specialization || "",
          qualification: res.data.qualification || "",
          experience: res.data.experience || "",
          aboutDoctor: res.data.aboutDoctor || "",
          profilePhoto: null, // don’t auto-fill file input
          profilePhotoUrl: res.data.profilePhoto || "",
        });
      } catch (error) {
        console.error("Error fetching doctor details:", error);
        toast.error("Failed to load doctor details");
      }
    };

    getDoctorDetailsByID();
  }, [id]);

  // Submit updated doctor
  const handleSubmit = async (e) => {
    e.preventDefault();

    const centreId = localStorage.getItem("centreId");
    if (!centreId) {
      toast.error("Centre ID not found in local storage");
      return;
    }

    // Raw object first
    const rawData = {
      name: doctor.name,
      specialization: doctor.specialization,
      qualification: doctor.qualification,
      centreId,
      experience: doctor.experience,
      aboutDoctor: doctor.aboutDoctor,
      profilePhoto: doctor.profilePhoto,
    };

    // Convert to FormData & filter out null/empty
    const formData = new FormData();
    Object.entries(rawData).forEach(([key, value]) => {
      if (value !== null && value !== undefined && value !== "") {
        formData.append(key, value);
      }
    });

    // Debug
    for (let [key, value] of formData.entries()) {
    }

    try {
      const response = await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/doctors/edit-doctor/${id}`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data", // required for file uploads
          },
        }
      );

      if (response.status === 200) {
        toast.success("Doctor updated successfully!");
        setTimeout(() => {
          navigate("/doctors");
        }, 3000);
      }
    } catch (error) {
      console.error("Error updating doctor:", error);
      toast.error("Failed to update doctor");
    }
  };

  const [categoryServices, setCategoryServices] = useState([]);

  const getCategories = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/services/doctor-consultation`
      );

      setCategoryServices(res.data);
    } catch (error) {
      console.error("Error to fetch the details");
    }
  };

  useEffect(() => {
    getCategories();
  }, []);

  return (
    <div className="dashboard-body__content">
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div className="card-body">
              <h3 className="mb-4">Edit Doctor</h3>
              <form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="row g-3">
                  {/* Profile Photo */}
                  <div className="col-md-6">
                    <label className="form-label">Profile Photo(Note: File size should be less than 2MB)</label>
                    <input
                      type="file"
                      accept="image/*"
                      onChange={handleFileChange}
                      className="form-control"
                      required={!doctor.profilePhotoUrl}
                    />

                    {/* Show existing image if available */}
                    {doctor.profilePhotoUrl && (
                      <div style={{ marginTop: "10px" }}>
                        <img
                          src={doctor.profilePhotoUrl}
                          alt="Doctor"
                          style={{ width: "120px", borderRadius: "8px" }}
                        />
                      </div>
                    )}
                  </div>

                  {/* Name */}
                  <div className="col-md-6">
                    <label className="form-label">Name</label>
                    <input
                      type="text"
                      name="name"
                      value={doctor.name}
                      onChange={handleChange}
                      className="form-control"
                      required
                    />
                  </div>

                  {/* Specialization */}
                  <div className="col-md-6">
                    <label className="form-label">Specialization</label>
                    <Select
                      options={
                        categoryServices?.services?.map((service) => ({
                          value: service.serviceName,
                          label: service.serviceName,
                        })) || []
                      }
                      value={
                        doctor.specialization
                          ? {
                              value: doctor.specialization,
                              label: doctor.specialization,
                            }
                          : null
                      }
                      onChange={(selectedOption) =>
                        setDoctor((prev) => ({
                          ...prev,
                          specialization: selectedOption
                            ? selectedOption.value
                            : "",
                        }))
                      }
                      placeholder="Select specialization"
                      menuPortalTarget={document.body}
                      styles={{
                        menuPortal: (base) => ({
                          ...base,
                          zIndex: 9999,
                        }),
                        menuList: (base) => ({
                          ...base,
                          maxHeight: "250px", // limit height
                          overflowY: "auto", // enable scroll
                        }),
                      }}
                      required
                    />
                  </div>

                  {/* Qualification */}
                  <div className="col-md-6">
                    <label className="form-label">Qualification</label>
                    <input
                      type="text"
                      name="qualification"
                      value={doctor.qualification}
                      onChange={handleChange}
                      className="form-control"
                      required
                    />
                  </div>

                  {/* Experience */}
                  <div className="col-md-6">
                    <label className="form-label">Experience</label>
                    <input
                      type="text"
                      name="experience"
                      value={doctor.experience}
                      onChange={handleChange}
                      className="form-control"
                      required
                    />
                  </div>

                  {/* About Doctor */}
                  <div className="col-md-12">
                    <label className="form-label">About Doctor</label>
                    <input
                      type="text"
                      name="aboutDoctor"
                      value={doctor.aboutDoctor}
                      onChange={handleChange}
                      className="form-control"
                      required
                    />
                  </div>
                </div>

                <div className="mt-4">
                  <button
                    type="submit"
                    className="btn btn-main"
                    style={{
                      backgroundColor: "green",
                      color: "whitesmoke",
                      padding: "10px 20px",
                      borderRadius: "5px",
                      fontWeight: "600",
                    }}
                  >
                    Update Doctor
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default EditDoctor;
